<template>
  <v-layout>
    <v-flex class="ml-2 mt-2 mr-2">
      <v-layout row>
        <h1 class="mt-2 mb-2">Startup Dashboard</h1>
      </v-layout>
      <v-layout row wrap v-if="loading">
        <v-flex xs12 class="text-xs-center">
          <v-progress-circular 
          indeterminate 
          color="green"
          :width="7"
          :size="70"
          ></v-progress-circular>
        </v-flex>
      </v-layout>
      <v-layout row wrap v-if="checkCompaniesLoad">
        <v-flex 
        xs12 sm6 md4
        v-for="company in companies"
        :key="company.id">
          <v-card class="mr-2 mb-2">
            <v-card-media
            :src="company.imageUrl"
            height="200px"
            contain
            >
            </v-card-media>
            <v-card-title primary-title>
              <div>
                <h2 class="mb-2">{{company.name}}</h2>
                <p class="mb-2">{{company.description}}</p>
                <p class="mb-2">{{company.website}}</p>
              </div>
            </v-card-title>
            <v-card-actions>
              <v-btn 
              flat 
              color="success"
              :to="'/companies/' + company.id"
              >Explore</v-btn>
            </v-card-actions>
          </v-card>
        </v-flex>
      </v-layout>
    </v-flex>
  </v-layout>
</template>

<script>
export default {
  computed: {
    companies () {
      return this.$store.getters.loadedCompanies
    },
    checkCompaniesLoad() {
      return this.companies != null ? true : false
    },
    loading () {
      return this.$store.getters.loading
    }
  }
}
</script>




