<template>
  <v-dialog
  fullscreen
  hide-overlay
  transition="dialog-bottom-transition"
  v-model="fundingDialog">
    <v-btn 
    fab
    class="accent"
    slot="activator">  
      <v-icon>attach_money</v-icon>
    </v-btn>
    <v-card>
      <v-container fluid>
        <v-toolbar card dark color="accent">
          <v-btn icon dark @click="fundingDialog = false">
            <v-icon>close</v-icon>
          </v-btn>
          <v-toolbar-title>Funding Rounds for {{company.name}}</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn 
            dark 
            flat 
            @click="addFundingDialog = true">Add Funding Round</v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <v-layout row wrap>
          <v-flex xs12>
            <v-data-table
              :headers="headers"
              :items="desserts"
              hide-actions
              class="elevation-1"
              scrollable
            >
              <template slot="items" slot-scope="props">
                <td>{{ props.item.name }}</td>
                <td class="text-xs-right">{{ props.item.calories }}</td>
                <td class="text-xs-right">{{ props.item.fat }}</td>
                <td class="text-xs-right">{{ props.item.carbs }}</td>
                <td class="text-xs-right">{{ props.item.protein }}</td>
                <td class="justify-center layout px-0">
                  <v-btn icon class="mx-0" @click="editItem(props.item)">
                    <v-icon color="teal">edit</v-icon>
                  </v-btn>
                  <v-btn icon class="mx-0" @click="deleteItem(props.item)">
                    <v-icon color="pink">delete</v-icon>
                  </v-btn>
                </td>
              </template>
            </v-data-table>
          </v-flex>
        </v-layout>
        <v-divider></v-divider>
        <v-layout row wrap>
          <v-flex xs12>
            <v-card-actions>
              <v-btn 
              flat 
              success 
              class="success darken-1"
              @click="submitRound"
              >Save</v-btn>
              <v-btn 
              flat 
              class="blue--text darken-1"
              @click="fundingDialog = false">Close</v-btn>
            </v-card-actions>
          </v-flex>
        </v-layout>

  <!-- add round dialog -->
        <v-dialog
        v-model="addFundingDialog"
        max-width="300px">
          <v-card>
            <v-card-title>
              <span class="headline">New Funding Round</span>
            </v-card-title>
            <v-card-text>
              <v-container grid-list-md>
                <v-layout wrap>
                  <v-flex xs12 sm6 md4>
                    <v-text-field v-model="editedItem.name" label="Dessert name"></v-text-field>
                  </v-flex>
                  <v-flex xs12 sm6 md4>
                    <v-text-field v-model="editedItem.calories" label="Calories"></v-text-field>
                  </v-flex>
                  <v-flex xs12 sm6 md4>
                    <v-text-field v-model="editedItem.fat" label="Fat (g)"></v-text-field>
                  </v-flex>
                  <v-flex xs12 sm6 md4>
                    <v-text-field v-model="editedItem.carbs" label="Carbs (g)"></v-text-field>
                  </v-flex>
                  <v-flex xs12 sm6 md4>
                    <v-text-field v-model="editedItem.protein" label="Protein (g)"></v-text-field>
                  </v-flex>
                </v-layout>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn 
              color="blue darken-1" 
              flat @click="close"
              @click.native="addFundingDialog = false">Cancel</v-btn>
              <v-btn 
              color="blue darken-1" 
              flat 
              @click="save"
              @click.native="addFundingDialog = false"
              >Save</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
  <!-- end of add round dialog -->

      </v-container>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  props: ['companies', 'company', 'user'],
  data () {
    return {
      //set dialogs' states to false
      fundingDialog: false,
      addFundingDialog: false,
      dateDialog: false,
      //set params of add round form
      headers: [
        {
          text: 'Dessert (100g serving)',
          align: 'left',
          sortable: false,
          value: 'name'
        },
        { text: 'Calories', value: 'calories' },
        { text: 'Fat (g)', value: 'fat' },
        { text: 'Carbs (g)', value: 'carbs' },
        { text: 'Protein (g)', value: 'protein' },
        { text: 'Actions', value: 'name', sortable: false }
      ],
      desserts: [],
      editedIndex: -1,
      editedItem: {
        name: '',
        calories: 0,
        fat: 0,
        carbs: 0,
        protein: 0
      },
      defaultItem: {
        name: '',
        calories: 0,
        fat: 0,
        carbs: 0,
        protein: 0
      }
    }
  },
  computed: {
    //for editing funding round table
    formTitle () {
      return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
    },
  },
  watch: {
    dialog (val) {
      val || this.close()
    }
  },
  created () {
    this.initialize()
  },
  methods: {
    submitRound () {
      //check if any compulsory field is empty
      // if (this.location.trim() === '' 
      // || this.researchDate.trim() === '' 
      // || this.writeup.trim() === '') {
      //   alert('Please fill in all required fields.')
      //   return
      // }
      //close the dialog and dispatch payload to store
      this.$store.dispatch('createFundingRound', {
        // companyId: this.companyId,
        // name: this.name,
        // analyst: this.analyst,
        // location: this.location,
        // attachmentRaw: this.attachmentRaw,
        // researchDate: this.researchDate,
        // writeup: this.writeup,
        // researchType: this.researchType,
        // sentiment: this.sentiment,
        // addCompany: this.addCompany
      })
      // this.$router.push('/companies/' + this.companyId)
      this.researchDialog = false
    },
    //fill various select dropdowns and data tables
    initialize () {
      this.desserts = [
        {
          name: 'Frozen Yogurt',
          calories: 159,
          fat: 6.0,
          carbs: 24,
          protein: 4.0
        },
        {
          name: 'Ice cream sandwich',
          calories: 237,
          fat: 9.0,
          carbs: 37,
          protein: 4.3
        },
        {
          name: 'Eclair',
          calories: 262,
          fat: 16.0,
          carbs: 23,
          protein: 6.0
        },
        {
          name: 'Cupcake',
          calories: 305,
          fat: 3.7,
          carbs: 67,
          protein: 4.3
        },
        {
          name: 'Gingerbread',
          calories: 356,
          fat: 16.0,
          carbs: 49,
          protein: 3.9
        },
        {
          name: 'Jelly bean',
          calories: 375,
          fat: 0.0,
          carbs: 94,
          protein: 0.0
        },
        {
          name: 'Lollipop',
          calories: 392,
          fat: 0.2,
          carbs: 98,
          protein: 0
        },
        {
          name: 'Honeycomb',
          calories: 408,
          fat: 3.2,
          carbs: 87,
          protein: 6.5
        },
        {
          name: 'Donut',
          calories: 452,
          fat: 25.0,
          carbs: 51,
          protein: 4.9
        },
        {
          name: 'KitKat',
          calories: 518,
          fat: 26.0,
          carbs: 65,
          protein: 7
        }
      ]
    },
    //CRUDs for funding round dialog
    editItem (item) {
      this.editedIndex = this.desserts.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },
    deleteItem (item) {
      const index = this.desserts.indexOf(item)
      confirm('Are you sure you want to delete this item?') && this.desserts.splice(index, 1)
    },
    close () {
      this.dialog = false
      setTimeout(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      }, 300)
    },
    save () {
      if (this.editedIndex > -1) {
        Object.assign(this.desserts[this.editedIndex], this.editedItem)
      } else {
        this.desserts.push(this.editedItem)
      }
      this.close()
    }
  }
}
</script>




