<template>
  <v-container fluid>
    <v-layout row wrap v-if="loading">
      <v-flex xs12 class="text-xs-center">
        <v-progress-circular 
        indeterminate 
        color="green"
        :width="7"
        :size="70"
        v-if="loading"></v-progress-circular>
      </v-flex>
    </v-layout>
    <v-layout row wrap v-if="checkResearchLoad">
      <v-flex xs12 sm8 offset-sm2>
        <v-btn 
          flat 
          color="success"
          @click="$router.go(-1)"
          ><v-icon>arrow_back</v-icon>
           Back to company page
        </v-btn>
        <v-card class="pt-2">
          <v-card-media
            :src="doc.attachmentUrl"
            height="300px"
            contain
          >
          </v-card-media>
          <v-card-title primary-title>
            <div>
              <div class="headline">{{doc.researchType}}</div>
              <div class="green--text">By: {{doc.analyst}}</div>
              <span class="grey--text">{{doc.location}} | {{doc.researchDate}}</span>
            </div>
          </v-card-title>
          <v-card-actions>
            <p class="pl-2">Sentiment: 
              <v-chip color="secondary" text-color="white">{{doc.sentiment}}</v-chip>
            </p>
            <v-spacer></v-spacer>
            <template v-if="userIsCreator">
              <v-tooltip bottom>
                <app-edit-research-dialog
                slot="activator"
                :doc="doc"
                :user="user">
                </app-edit-research-dialog>  
                <span>Edit Research Document</span>
              </v-tooltip> 
            </template>
            <v-tooltip bottom>
              <v-btn 
              slot="activator" 
              icon 
              @click="show = !show">
                <v-icon>{{ show ? 'keyboard_arrow_up' : 'keyboard_arrow_down' }}</v-icon>
              </v-btn>
              <span>View Research writeup</span>
            </v-tooltip>
          </v-card-actions>
          <v-slide-y-transition>
            <v-card-text v-show="show">
              {{doc.writeup}}
            </v-card-text>
          </v-slide-y-transition>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  props: ['id', 'company'],
  data () {
    return {
      show: true
    }
  },
  computed: {
    doc () {
      return this.$store.getters.loadedResearchDoc(this.id)
    },
    checkResearchLoad() {
      return this.doc != null ? true : false
    },
    //for retrieving user data
    user () {
      return this.$store.getters.user
    },
    //for retrieving loading state
    loading () {
      return this.$store.getters.loading
    },
    //for retrieving user login state
    userAuthenticated () {
      return this.$store.getters.user !== null 
      && this.$store.getters.user !== undefined
    },
    //check if user is creator of research doc
    userIsCreator () {
      if (!this.userAuthenticated) {
        return false
      }
      return this.$store.getters.user.id === this.doc.creatorId
    }
  }
}
</script>




