<template>
  <v-dialog
  fullscreen
  hide-overlay
  transition="dialog-bottom-transition"
  v-model="editResearchDialog">
    <v-btn 
    fab
    accent
    slot="activator">  
      <v-icon>edit</v-icon>
    </v-btn>
    <v-card>
      <v-container fluid>
        <v-toolbar card dark color="secondary">
          <v-btn icon dark @click.native="editResearchDialog = false">
            <v-icon>close</v-icon>
          </v-btn>
          <v-toolbar-title>Edit Research Note</v-toolbar-title>
          <v-spacer></v-spacer>
          <h3>(* Required Item)</h3>          
          <v-toolbar-items>
            <v-btn 
            dark 
            flat 
            @click="submitEditedResearch">Save</v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <v-layout row wrap>
          <v-flex xs12>
            <v-card-text>
              <v-layout row>
                <v-flex xs6 class="mr-2">
                  <v-select
                    :items="researchType"
                    v-model="editedResearchType"
                    label="Research Type"
                    item-value="text"
                    color="success" 
                    required
                  ></v-select>
                </v-flex>
                <v-flex xs6 class="mr-2">
                  <v-select
                    :items="addCompany"
                    v-model="editedAddCompany"
                    label="Tag Another Company"
                    item-value="text"
                    color="success"
                  ></v-select>
                </v-flex>
                <v-flex xs6>
                  <v-select
                    :items="sentiment"
                    v-model="editedSentiment"
                    label="Sentiment"
                    item-value="text"
                    color="success"    
                  ></v-select>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs6 class="mr-2">
                  <v-text-field 
                  v-model="analyst"  
                  name="analyst"
                  label="Analyst Email"
                  disabled>
                  </v-text-field>
                </v-flex>
                <v-flex xs6>
                  <v-text-field 
                  v-model="editedLocation"
                  name="location"
                  label="Location"
                  color="success"
                  :rules="[v => !!v || 'Event location is required']"
                  required>
                  </v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs6 class="mr-2">
                  <v-btn 
                  raised
                  class="success"
                  @click="editedFile">Upload Attachment
                    <v-icon right dark>cloud_upload</v-icon>
                  </v-btn>
                  <input 
                  type="file" 
                  v-show="false"
                  ref="fileInput"
                  accept="image/*"
                  @change="fileEdited">
                </v-flex>
                <v-flex xs6>
                  <v-layout row>
                    <v-btn
                    flat
                    class="success"
                    @click="dateDialog = true">Edit Date</v-btn>
                    <p 
                    class="mt-3"
                    v-if="editedDate !== ''">Date Selected: {{editedDate | dateFilter}}</p>
                    <v-dialog
                    v-model="dateDialog"
                    max-width="250px">
                      <v-date-picker 
                      v-model="editedDate" 
                      name="editedDate"     
                      id="editedDate"
                      color="success"
                      required>
                      </v-date-picker>
                    </v-dialog>
                  </v-layout>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs6 class="mr-2">
                  <p>Attachment Preview:</p>
                  <img 
                  :src="editedAttachmentUrl"
                  height="250px">
                </v-flex>
                <v-flex xs6>
                  <v-text-field 
                  v-model="editedWriteup" 
                  name="writeup"
                  label="Writeup"
                  color="success" 
                  placeholder="Max 500 characters"
                  multi-line
                  rows="10"
                  :rules="[(v) => v.length <= 500 || 'Max 500 characters']"
                  :counter="500"
                  required>
                  </v-text-field>
                </v-flex>
              </v-layout>
            </v-card-text>
          </v-flex>
        </v-layout>
        <v-divider></v-divider>
        <v-layout row wrap>
          <v-flex xs12>
            <v-card-actions>
              <v-btn 
              flat 
              success 
              class="success darken-1"
              @click="submitEditedResearch"
              >Save</v-btn>
              <v-btn 
              flat 
              class="blue--text darken-1"
              @click.native="editResearchDialog = false">Close</v-btn>
            </v-card-actions>
          </v-flex>
        </v-layout>
      </v-container>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  props: ['doc', 'user'],
  data () {
    return {
      //set dialogs' states to false
      editResearchDialog: false,
      dateDialog: false,
      //set params of add research form
      analyst: this.user.email,
      editedLocation: this.doc.location,
      editedAttachmentUrl: '',
      editedAttachmentRaw: null,
      editedDate: null,
      editedWriteup: this.doc.writeup,
      researchType: ['Meeting Note', 'Investor Relations Document', 'Legal Document', 'Financial Report Note', 'Call Note', 'Research Trip Note', 'Ad Hoc Research Note'],
      editedResearchType: '',
      sentiment: ['Excellent', 'Good', 'Neutral', 'Bad', 'Terrible'],
      editedSentiment: '',
      addCompany: [],
      editedAddCompany: []
    }
  },
  methods: {
    //for retrieving all companies
    companies () {
      return this.$store.getters.loadedCompanies
    },
    //fill addCompany array
    initialize () {
      this.companies.forEach(company => {
        this.addCompany.push({
          text: company.name
        })
      })
    },
    //for attachment input functions
    editedFile () {
      this.$refs.fileInput.click()
    },
    fileEdited (event) {
      const files = event.target.files
      let filename = files[0].name
      if (filename.lastIndexOf('.') <= 0) {
        return alert('Please select a valid file.')
      }
      const fileReader = new FileReader() 
      //listen to async fileReader conversion process
      fileReader.addEventListener('load', () => {
        this.editedAttachmentUrl = fileReader.result
      })
      //async conversion of file to base-64 string
      fileReader.readAsDataURL(files[0])
      this.editedAttachmentRaw = files[0]
    },
    submitEditedResearch () {
      //check if any compulsory field is empty
      if (this.editedLocation.trim() === '' 
      || this.editedWriteup.trim() === '') {
        alert('Please fill in all required fields.')
        return
      }
      //close the dialog and dispatch payload to store
      this.$store.dispatch('editResearch', {
        id: this.doc.id,
        location: this.editedLocation,
        attachmentRaw: this.editedAttachmentRaw,
        writeup: this.editedWriteup,
        researchType: this.editedResearchType,
        sentiment: this.editedSentiment,
        addCompany: this.editedAddCompany,
        researchDate: this.editedDate,
        changeDate: new Date().toISOString().substr(0,10)
      })
      this.$router.push('/research/' + this.doc.id)
      this.editResearchDialog = false
    }
  },
  created () {
    this.editedDate = new Date(this.doc.researchDate).toISOString().substr(0,10)
    this.editedAttachmentUrl = this.doc.attachmentUrl
  }
 }
</script>




