<template>
  <v-container>
    <v-layout row>
      <v-flex xs12 sm6 offset-sm3>
        <h2 class="success--text">Add A New Startup</h2>
      </v-flex>
    </v-layout>
    <v-layout row>
      <v-flex xs12>
        <v-form 
        ref="form"
        v-model="valid"
        lazy-validation>
          <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
              <v-text-field 
              v-model="name"              
              name="name"
              color="success"
              label="Company Name"
              :rules="[v => !!v || 'Company name is required']"
              required>
              </v-text-field>
            </v-flex>
          </v-layout>
          <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
              <v-text-field 
              v-model="hq"                            
              name="hq"
              color="success"              
              label="Headquarter"
              :rules="[v => !!v || 'Company hq is required']"
              required>
              </v-text-field>
            </v-flex>
          </v-layout>
          <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
              <v-btn 
              raised
              class="success"
              @click="selectFile">Upload Image
                <v-icon right dark>cloud_upload</v-icon>
              </v-btn>
              <input 
              type="file" 
              v-show="false"
              ref="fileInput"
              accept="image/*"
              @change="fileSelected">
            </v-flex>
          </v-layout>
          <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
              <p>Image Preview:</p>
              <img 
              :src="image"
              width="100%">
            </v-flex>
          </v-layout>
          <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
              <v-text-field 
              v-model="description"                  
              name="description"
              color="success"              
              label="Description"
              placeholder="Max 250 characters"
              multi-line
              rows="5"
              :rules="[(v) => v.length <= 250 || 'Max 250 characters']"
              :counter="250"
              required>
              </v-text-field>
            </v-flex>
          </v-layout>
          <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
              <v-text-field 
              v-model="website"                      
              name="website" 
              color="success"              
              label="Company website URL"
              :rules="[v => !!v || 'Website URL is required']" 
              id="website"
              required>
              </v-text-field>
            </v-flex>
          </v-layout>
          <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
              <v-text-field 
              v-model="founder"                      
              name="founder" 
              color="success"              
              label="Founder(s)"
              :rules="[v => !!v || 'Founder name(s) is required']"
              id="founder"
              required>
              </v-text-field>
            </v-flex>
          </v-layout>
        </v-form>
        <v-layout row>
          <v-flex xs12 sm6 offset-sm3>
            <v-btn 
            class="success" 
            :disabled="!valid"
            @click="submit">Add Startup</v-btn>
            <v-btn 
            class="accent" 
            @click="clear">Clear</v-btn>
          </v-flex>
        </v-layout>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data: () => ({
    valid: false,
    name: '',
    hq: '',
    image: '',
    description: '',
    website: '',
    founder: '',
    imageRaw: null
  }),
  methods: {
    submit () {
      if (!this.imageRaw) {
        return
      }
      if (this.$refs.form.validate()) {
        const companyData = {
          name: this.name,
          hq: this.hq,
          imageRaw: this.imageRaw,
          description: this.description,
          website: this.website,
          founder: this.founder,
          createDate: new Date()
        }
        this.$store.dispatch('createCompany', companyData)
        this.$router.push('/companies')
      }
    },
    clear () {
      this.$refs.form.reset()
    },
    //simulate input file click
    selectFile () {
      this.$refs.fileInput.click()
    },
    //upload image method
    fileSelected (event) {
      const files = event.target.files
      let filename = files[0].name
      //check for file extension
      if (filename.lastIndexOf('.') <= 0) {
        return alert ('Please select a valid file.')
      }
      //turn file into base-64 string, readable by :src directive
      const fileReader = new FileReader()
      //add async event listener
      fileReader.addEventListener('load', () => {
        this.image = fileReader.result
      })
      fileReader.readAsDataURL(files[0])
      this.imageRaw = files[0]
    }
  }
}
</script>




