<template>
  <v-container fluid>
    <v-layout row wrap v-if="loading">
      <v-flex xs12 class="text-xs-center">
        <v-progress-circular 
        indeterminate 
        color="green"
        :width="7"
        :size="70"
        v-if="loading"></v-progress-circular>
      </v-flex>
    </v-layout>
    <v-layout row wrap v-if="checkCompanyLoad">
      <v-flex xs12>
        <v-layout row wrap>

        <!-- Company details card -->          
          <v-flex xs12 sm12 md4>
            <v-card class="info--text mr-2 mb-2">
              <v-container fluid grid-list-lg>
                <v-layout row>
                  <v-flex xs12 sm12 md5>
                    <v-card-media 
                    :src="company.imageUrl"
                    height="200px"
                    contain
                    ></v-card-media>
                  </v-flex>
                  <v-flex xs12 sm12 md7>
                    <div>
                      <div class="headline">{{company.name}}</div>
                      <span>{{company.website}}</span>
                      <v-divider class="mt-2 mb-2"></v-divider>
                      <div class="black--text mb-2">Headquarter: {{company.hq}}</div>
                      <div class="success--text mb-2">Founder(s): {{company.founder}}</div>
                      <div class="success--text mb-2">Coverage analyst(s): {{company.analystEmail}}</div>
                      <div class="black--text mb-2">Date Added: {{company.createDate | dateFilter}}</div>
                      <div class="black--text mb-2">Last Edited: {{company.editedDate | dateFilter}}</div>
                    </div>
                  </v-flex>
                </v-layout>
              </v-container>
              <v-card-actions class="pb-4">
                <v-spacer></v-spacer>    
                <template>
                  <v-tooltip bottom>
                    <app-add-research-dialog
                    slot="activator"
                    :companies="companies"
                    :company="company"
                    :user="user"></app-add-research-dialog>
                    <span>Add Research Note</span>
                  </v-tooltip>
                </template>            
                <template>
                  <v-tooltip bottom>
                    <app-funding-round-dialog
                    slot="activator"
                    :companies="companies"
                    :company="company"
                    :user="user"></app-funding-round-dialog>
                    <span>View Funding Round(s)</span>
                  </v-tooltip>
                </template>
                <template v-if="userIsCreator">
                  <v-tooltip bottom>
                    <app-edit-company-dialog
                    slot="activator"
                    :company="company"
                    :user="user"></app-edit-company-dialog>
                    <span>Edit Startup</span>
                  </v-tooltip>
                </template>
                <v-tooltip bottom>
                  <v-btn 
                  slot="activator" 
                  icon 
                  @click="show = !show">
                    <v-icon>{{ show ? 'keyboard_arrow_up' : 'keyboard_arrow_down' }}</v-icon>
                  </v-btn>
                  <span>View Company Description</span>
                </v-tooltip>
              </v-card-actions>
              <v-slide-y-transition>
                <v-card-text v-show="show">
                  <v-divider class="mb-2"></v-divider> 
                  <div class="headline">Company Description</div>
                  <p class="black--text">{{company.description}}</p>
                </v-card-text>
              </v-slide-y-transition>
            </v-card>
          </v-flex>
        <!-- End of company details card -->

        <!-- Research doc search card -->
          <v-flex xs12 sm12 md8>
            <v-toolbar 
            color="white" 
            extended>
              <v-toolbar-title class="mr-2">Research on {{company.name}}</v-toolbar-title>
              <v-flex xs10 offset-xs1 slot="extension">
                <v-select
                :items="researchType"
                color="success"
                prepend-icon="search"
                v-model="filterInput"
                label="Search by research type"
                autocomplete
                ></v-select>
              </v-flex>
            </v-toolbar>
            <v-layout 
            row 
            wrap>
              <v-flex xs12>
                <v-list two-line subheader>
                  <v-subheader inset>Research Documents</v-subheader>
                  <template 
                  v-for="doc in filterByResearchType(docs, filterInput)" 
                  >
                    <v-list-tile 
                    :key="doc.id"
                    avatar
                    @click=""
                    :to="'/research/' + doc.id"
                    :company="company">
                      <v-list-tile-avatar>
                        <img :src="doc.attachmentUrl">
                      </v-list-tile-avatar>
                      <v-list-tile-content>
                        <v-list-tile-title v-html="doc.researchType">
                        </v-list-tile-title>
                        <v-list-tile-sub-title class="text--primary">By: {{doc.analyst}}</v-list-tile-sub-title>
                        <v-list-tile-sub-title v-html="doc.writeup"></v-list-tile-sub-title>
                      </v-list-tile-content>
                      <v-list-tile-action>
                        <v-list-tile-action-text>
                          {{doc.researchDate}}
                        </v-list-tile-action-text>
                        <v-chip
                        color="secondary"
                        text-color="white">{{doc.sentiment}}
                        </v-chip>
                      </v-list-tile-action>
                    </v-list-tile>
                  </template>
                </v-list>
              </v-flex>
            </v-layout>
          </v-flex>
         <!-- End of research doc search card -->

        </v-layout>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  props: ['id'],
  data () {
    return {
      //for showing company description
      show: false,
      //for research doc select autocomplete
      researchType: ['Meeting Note', 'Investor Relations Document', 'Legal Document', 'Financial Report Note', 'Call Note', 'Research Trip Note', 'Ad Hoc Research Note'],
      filterInput: ''
    }
  },
  computed: {
    //for retrieving all companies
    companies () {
      return this.$store.getters.loadedCompanies
    },
    //for retrieving specific company data
    company () {
      return this.$store.getters.loadedCompany(this.id)
    },
    checkCompanyLoad() {
        return this.company != null ? true : false
    },
    //for retrieving research data
    docs () {
      return this.$store.getters.loadedResearchDocs
    },
    //for retrieving user data
    user () {
      return this.$store.getters.user
    },
    //for retrieving loading state
    loading () {
      return this.$store.getters.loading
    },
    //for retrieving user login state
    userAuthenticated () {
      return this.$store.getters.user !== null 
      && this.$store.getters.user !== undefined
    },
    //check if user is creator of company profile
    userIsCreator () {
      if (!this.userAuthenticated) {
        return false
      }
      return this.$store.getters.user.id === this.company.analystId
    }
  },
  methods: {
    //filter research docs by research type
    filterByResearchType (list, value) {
      //Upper case first letter in name search
      value = value.charAt(0).toUpperCase() + value.slice(1) 
      return list.filter((doc)=> {
        return doc.researchType.indexOf(value) > -1
      })
    }
  }
}
</script>




