import Vue from 'vue'
import Vuex from 'vuex'
import * as firebase from 'firebase'

Vue.use(Vuex)

export const store = new Vuex.Store({
  state: {
    loadedCompanies: [],
    loadedResearchDocs: [],
    user: null,
    loading: false,
    error: null
  },
  mutations: {
    createCompany (state, payload) {
      state.loadedCompanies.push(payload)
    },
    setLoadedCompanies (state, payload) {
      state.loadedCompanies = payload
    },
    updateCompany (state, payload) {
      const company = state.loadedCompanies.find(company => {
        return company.id === payload.companyId
      })
      company.editedDate = payload.editedDate
      if (payload.name) {
        company.name = payload.name
      }
      if (payload.hq) {
        company.hq = payload.hq
      }
      if (payload.description) {
        company.description = payload.description
      }
      if (payload.website) {
        company.website = payload.website
      }
    }, 
    createResearchDocs (state, payload) {
      state.loadedResearchDocs.push(payload)
    },
    setLoadedResearchDocs (state, payload) {
      state.loadedResearchDocs = payload
    },
    updateResearchDoc (state, payload) {
      const doc = state.loadedResearchDocs.find(doc => {
        return doc.id === payload.id
      })
      if (payload.researchType) {
        doc.researchType = payload.researchType
      }
      if (payload.addCompany) {
        doc.addCompany = payload.addCompany
      }
      if (payload.sentiment) {
        doc.sentiment = payload.sentiment
      }
      if (payload.location) {
        doc.location = payload.location
      }
      if (payload.writeup) {
        doc.writeup = payload.writeup
      }
      if (payload.researchDate) {
        doc.researchDate = payload.researchDate
      }
    },  
    setUser (state, payload) {
      state.user = payload
    },
    setLoading (state, payload) {
      state.loading = payload
    },
    setError (state, payload) {
      state.error = payload
    },
    clearError (state) {
      state.error = null
    }
  },
  //do async tasks like talk to server in "actions" only
  actions: {
    createCompany ({commit, getters}, payload) {
      const company = {
        name: payload.name,
        hq: payload.hq,
        description: payload.description,
        website: payload.website,
        founder: payload.founder,
        createDate: payload.createDate.toISOString(),
        analystEmail: getters.user.email,
        analystId: getters.user.id
      }
      let key
      let imageUrl
      //create company key first in firebase DB      
      firebase.database().ref('companies').push(company)
      .then((data) => {
        key = data.key 
        return key       
      })
      //store image in firebase storage
      .then (key => {
        const filename = payload.imageRaw.name
        const ext = filename.slice(filename.lastIndexOf('.'))
        return firebase.storage().ref('companies/' + 'c' + key + ext).put(payload.imageRaw)
      })
      //retrieve image Url to store as company property in firebase DB
      .then(fileData => {
        fileData.ref.getDownloadURL()
        .then(url => {
          imageUrl = url
          console.log('File available at', imageUrl);
          return firebase.database().ref('companies').child(key).update({imageUrl: url})
        })
      })
      //commit to local store
      .then (() => {
        commit('createCompany', {
          ...company,
          imageUrl: imageUrl,
          id: key
        }) 
      })
      .catch(
        (error) => {
          console.log(error)      
        }
      )
    },
    loadCompanies ({commit}) {
      commit('setLoading', true)
      firebase.database().ref('companies').once('value')
      .then(data => {
        const companies = []
        const obj = data.val()
        for (let key in obj) {
          companies.push({
            id: key,
            name: obj[key].name,
            hq: obj[key].hq,
            imageUrl: obj[key].imageUrl,
            description: obj[key].description,
            website: obj[key].website,
            founder: obj[key].founder,
            createDate: obj[key].createDate,
            analystEmail: obj[key].analystEmail,
            analystId: obj[key].analystId
          })
        }
        commit('setLoadedCompanies', companies)
        commit('setLoading', false)
      })
      .catch(error => {
        console.log(error)
        commit('setLoading', false)
      })
    },
    editCompany ({commit}, payload) {
      commit('setLoading', true)
      const editedObj = {}
      editedObj.editedDate = payload.editedDate
      if (payload.name) {
        editedObj.name = payload.name
      }
      if (payload.hq) {
        editedObj.hq = payload.hq
      }
      if (payload.description) {
        editedObj.description = payload.description
      }
      if (payload.website) {
        editedObj.website = payload.website
      }
      firebase.database().ref('companies').child(payload.companyId).update(editedObj)
      .then(() => {
        commit('setLoading', false)
        commit('updateCompany', payload)
      })
      .catch(error => {
        console.log(error)
        commit('setLoading', false)        
      })
    },
    createResearch ({commit, getters}, payload) {
      const doc = {
        companyId: payload.companyId,
        name: payload.name,        
        analyst: payload.analyst,    
        location: payload.location,    
        researchDate: payload.researchDate,  
        writeup: payload.writeup,              
        researchType: payload.researchType,
        sentiment: payload.sentiment,        
        addCompany: payload.addCompany,
        creatorId: getters.user.id,
        createDate: payload.createDate
      }
      let attachmentUrl
      let key
      //create research item first in firebase DB      
      firebase.database().ref('research').push(doc)
      .then((data) => {
        key = data.key 
        return key       
      })
      .then(key => {
        const filename = payload.attachmentRaw.name
        const ext = filename.slice(filename.lastIndexOf('.'))
        return firebase.storage().ref('research/' + 'r' + payload.createDate + '.' + key + ext).put(payload.attachmentRaw)
      })
      .then(fileData => {
        fileData.ref.getDownloadURL()
        .then(url => {
          attachmentUrl = url
          console.log('Attachment url available at', url)
          return firebase.database().ref('research').child(key).update({attachmentUrl: url})
        })
      })
      .then(() => {
        commit('createResearchDocs', {
          ...doc,
          attachmentUrl: attachmentUrl,
          id: key
        })
      })
      .catch((error) => {
        console.log(error)    
      })
    },
    loadResearchDocs ({commit, getters}) {
      commit('setLoading', true)
      firebase.database().ref('research').once('value')
      .then(data => {
        const docs = []
        const obj = data.val()
        for (let key in obj) {
          docs.push({
            id: key,
            name: obj[key].name,
            companyId: obj[key].companyId,
            researchType: obj[key].researchType,
            addCompany: obj[key].addCompany,
            researchDate: obj[key].researchDate,
            analyst: getters.user.email,
            location: obj[key].location,
            writeup: obj[key].writeup,
            attachmentUrl: obj[key].attachmentUrl,
            sentiment: obj[key].sentiment,
            creatorId: getters.user.id,
            createDate: obj[key].createDate
          })
        }
        commit('setLoadedResearchDocs', docs)
        commit('setLoading', false) 
      })
      .catch(error => {
        console.log(error)
        commit('setLoading', false)
      })
    },
    editResearch ({commit}, payload) {
      commit('setLoading', true)
      const editedObj = {}
      editedObj.changeDate = payload.changeDate
      if (payload.researchType) {
        editedObj.researchType = payload.researchType
      }
      if (payload.addCompany) {
        editedObj.addCompany = payload.addCompany
      }
      if (payload.sentiment) {
        editedObj.sentiment = payload.sentiment
      }
      if (payload.location) {
        editedObj.location = payload.location
      }
      if (payload.writeup) {
        editedObj.writeup = payload.writeup
      }
      if (payload.researchDate) {
        editedObj.researchDate = payload.researchDate
      }
      if (payload.attachmentRaw) {
        let attachmentUrl
        const filename = payload.attachmentRaw.name
        const ext = filename.slice(filename.lastIndexOf('.'))
        firebase.storage().ref('research/' + 'r' + payload.changeDate + '.' + payload.id + ext).put(payload.attachmentRaw)
        .then(fileData => {
          fileData.ref.getDownloadURL()
          .then(url => {
            attachmentUrl = url
            console.log('Attachment url available at', url)
            return firebase.database().ref('research').child(payload.id).update({attachmentUrl: url})
          })
        })
      }
      firebase.database().ref('research').child(payload.id).update(editedObj)
      .then(() => {
        commit('setLoading', false)
        commit('updateResearchDoc', payload)
      })
      .catch(error => {
        console.log(error)
        commit('setLoading', false)        
      })
    },
    userLogin({commit}, payload) {
      const newUser = {
        email: payload.email,
        coveredCompanies: []        
      }
      commit('setLoading', true)
      commit('clearError')
      firebase.auth().signInWithEmailAndPassword(payload.email, payload.password)
      .then(user => {
        commit('setLoading', false)
        commit('setUser', {
          ...newUser,
          id: user.uid
        })
      })
      .catch(
        error => {
          commit('setLoading', false)
          commit('setError', error)
        }
      )
    },
    autoLogin ({commit}, payload) {
      commit('setUser', {
        id: payload.uid, 
        email: payload.email,
        coveredCompanies: []
      })
    },
    logout ({commit}) {
      firebase.auth().signOut()
      commit('setUser', null)
    },
    clearError ({commit}) {
      commit('clearError')
    }
  },
  getters: {
    loadedCompanies (state) {
      return state.loadedCompanies.sort((companyA, companyB) => {
        return companyA.date > companyB.date
      })
    },
    loadedCompany (state) {
      return (companyId) => {
        return state.loadedCompanies.find((company) => {
          return company.id === companyId
        })
      }
    },
    loadedResearchDocs (state) {
      return state.loadedResearchDocs.sort((docA, docB) => {
        return docA.date > docB.date
      })
    },
    loadedResearchDoc (state) {
      return (docId) => {
        return state.loadedResearchDocs.find((doc) => {
          return doc.id === docId
        })
      }
    },
    user (state) {
      return state.user
    },
    loading (state) {
      return state.loading
    },
    error (state) {
      return state.error
    }
  }
})


