<template>
  <v-dialog
  fullscreen
  hide-overlay
  transition="dialog-bottom-transition"
  v-model="editDialog">
    <v-btn 
    fab
    accent
    slot="activator">
      <v-icon>edit</v-icon>
    </v-btn>
    <v-card>
      <v-container fluid>
        <v-toolbar card dark color="secondary">
          <v-btn icon dark @click.native="editDialog = false">
            <v-icon>close</v-icon>
          </v-btn>
          <v-toolbar-title>Edit {{company.name}}</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn 
            dark 
            flat 
            @click="saveEdits">Save</v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <v-layout row wrap>
          <v-flex xs12>
            <v-card-text>
              <v-text-field 
              v-model="editedBy"
              name="editedBy"
              label="Edited By"
              disabled>
              </v-text-field>
              <v-text-field 
              v-model="editedName"
              name="editedName"
              color="success"
              label="Company Name"
              :rules="[v => !!v || 'Company name is required']"
              required>
              </v-text-field>
              <v-text-field 
              v-model="editedHq"
              name="editedHq"
              color="success"              
              label="Headquarter"
              :rules="[v => !!v || 'Company hq is required']"
              required>
              </v-text-field>
              <v-text-field 
              v-model="editedDescription"
              name="editedDescription"
              color="success"              
              label="Description"
              placeholder="Max 250 characters"
              multi-line
              rows="5"
              :rules="[(v) => v.length <= 250 || 'Max 250 characters']"
              :counter="250"
              required>
              </v-text-field>
              <v-text-field 
              v-model="editedWebsite"
              name="editedWebsite" 
              color="success"              
              label="Company website URL"
              :rules="[v => !!v || 'Website URL is required']" 
              id="website"
              required>
              </v-text-field>
            </v-card-text>
          </v-flex>
        </v-layout>
        <v-divider></v-divider>
        <v-layout row wrap>
          <v-flex xs12>
            <v-card-actions>
              <v-btn 
              flat 
              success 
              class="success darken-1"
              @click="saveEdits"
              >Save</v-btn>
              <v-btn 
              flat 
              class="blue--text darken-1"
              @click.native="editDialog = false">Close</v-btn>
            </v-card-actions>
          </v-flex>
        </v-layout>
      </v-container>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  props: ['company', 'user'],
  data () {
    return {
      editDialog: false,
      editedName: this.company.name,
      editedHq: this.company.hq,
      editedDescription: this.company.description,
      editedWebsite: this.company.website,
      editedBy: this.user.email
    }
  },
  methods: {
    saveEdits () {
      //check if any compulsory field is empty
      if (this.editedName.trim() === '' 
      || this.editedHq.trim() === '' 
      || this.editedDescription.trim() === ''
      || this.editedWebsite.trim() === '') {
        alert('Please fill in all required fields.')
        return
      }
      //close the dialog
      this.$store.dispatch('editCompany', {
        companyId: this.company.id,
        name: this.editedName,
        hq: this.editedHq,
        description: this.editedDescription,
        website: this.editedWebsite,
        editedDate: new Date()
      })
      this.editDialog = false
    }
  }
}
</script>




