<template>
  <v-dialog
  fullscreen
  hide-overlay
  transition="dialog-bottom-transition"
  v-model="researchDialog">
    <v-btn 
    fab
    class="success"
    slot="activator">  
      <v-icon>note_add</v-icon>
    </v-btn>
    <v-card>
      <v-container fluid>
        <v-toolbar card dark color="success">
          <v-btn icon dark @click="researchDialog = false">
            <v-icon>close</v-icon>
          </v-btn>
          <v-toolbar-title>Research Note for {{company.name}}</v-toolbar-title>
          <v-spacer></v-spacer>
          <h3>(* Required Item)</h3>          
          <v-toolbar-items>
            <v-btn 
            dark 
            flat 
            @click="submitResearch">Save</v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <v-layout row wrap>
          <v-flex xs12>
            <v-card-text>
              <v-layout row>
                <v-flex xs12>
                  <v-text-field 
                  v-model="name"
                  label="Company Name"
                  name="name"
                  class="pr-5"
                  disabled
                  >
                  </v-text-field>
                </v-flex>
                <v-flex xs12>
                  <v-text-field 
                  v-model="companyId"
                  label="Company Id"
                  name="id"
                  disabled
                  >
                  </v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs6 class="mr-2">
                  <v-select
                    :items="researchType"
                    v-model="selectedResearchType"
                    label="Research Type"
                    item-value="text"
                    color="success" 
                    required
                  ></v-select>
                </v-flex>
                <v-flex xs6 class="mr-2">
                  <v-select
                    :items="addCompany"
                    v-model="selectedAddCompany"
                    label="Tag Another Company"
                    item-value="text"
                    color="success"
                  ></v-select>
                </v-flex>
                <v-flex xs6>
                  <v-select
                    :items="sentiment"
                    v-model="selectedSentiment"
                    label="Sentiment"
                    item-value="text"
                    color="success"    
                  ></v-select>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs6 class="mr-2">
                  <v-text-field 
                  v-model="analyst"  
                  name="analyst"
                  label="Analyst Email"
                  disabled>
                  </v-text-field>
                </v-flex>
                <v-flex xs6>
                  <v-text-field 
                  v-model="location"
                  name="location"
                  label="Location"
                  color="success"
                  :rules="[v => !!v || 'Event location is required']"
                  required>
                  </v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs6 class="mr-2">
                  <v-btn 
                  raised
                  class="success"
                  @click="selectFile">Upload Attachment
                    <v-icon right dark>cloud_upload</v-icon>
                  </v-btn>
                  <input 
                  type="file" 
                  v-show="false"
                  ref="fileInput"
                  accept="image/*"
                  @change="fileSelected">
                </v-flex>
                <v-flex xs6>
                  <v-layout row>
                    <v-btn
                    flat
                    class="success"
                    @click="dateDialog = true">Select Date</v-btn>
                    <p 
                    class="mt-3"
                    v-if="researchDate !== ''">Date Selected: {{researchDate | dateFilter}}</p>
                    <v-dialog
                    v-model="dateDialog"
                    max-width="250px">
                      <v-date-picker 
                      v-model="researchDate" 
                      name="researchDate"     
                      id="researchDate"
                      color="success"
                      required>
                      </v-date-picker>
                    </v-dialog>
                  </v-layout>
                </v-flex>
              </v-layout>
              <v-layout row>
                <v-flex xs6 class="mr-2">
                  <p>Attachment Preview:</p>
                  <img 
                  :src="attachment"
                  height="250px">
                </v-flex>
                <v-flex xs6>
                  <v-text-field 
                  v-model="writeup" 
                  name="writeup"
                  label="Writeup"
                  color="success" 
                  placeholder="Max 500 characters"
                  multi-line
                  rows="10"
                  :rules="[(v) => v.length <= 500 || 'Max 500 characters']"
                  :counter="500"
                  required>
                  </v-text-field>
                </v-flex>
              </v-layout>
            </v-card-text>
          </v-flex>
        </v-layout>
        <v-divider></v-divider>
        <v-layout row wrap>
          <v-flex xs12>
            <v-card-actions>
              <v-btn 
              flat 
              success 
              class="success darken-1"
              @click="submitResearch"
              >Save</v-btn>
              <v-btn 
              flat 
              class="blue--text darken-1"
              @click="researchDialog = false">Close</v-btn>
            </v-card-actions>
          </v-flex>
        </v-layout>
      </v-container>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  props: ['companies', 'company', 'user'],
  data () {
    return {
      //set dialogs' states to false
      researchDialog: false,
      dateDialog: false,
      //set params of add research form
      name: this.company.name,
      companyId: this.company.id,
      analyst: this.user.email,
      location: '',
      attachment: '',
      attachmentRaw: null,
      researchDate: '',
      writeup: '',
      researchType: ['Meeting Note', 'Investor Relations Document', 'Legal Document', 'Financial Report Note', 'Call Note', 'Research Trip Note', 'Ad Hoc Research Note'],
      selectedResearchType: '',
      sentiment: ['Excellent', 'Good', 'Neutral', 'Bad', 'Terrible'],
      selectedSentiment: '',
      addCompany: [],
      selectedAddCompany: []
    }
  },
  methods: {
    initialize () {
      this.companies.forEach(company => {
        if (company.id === this.companyId) {
          return
        }
        this.addCompany.push({
          text: company.name
        })
      })
    },
    //for attachment input functions
    selectFile () {
      this.$refs.fileInput.click()
    },
    fileSelected (event) {
      const files = event.target.files
      let filename = files[0].name
      if (filename.lastIndexOf('.') <= 0) {
        return alert('Please select a valid file.')
      }
      const fileReader = new FileReader() 
      //listen to async fileReader conversion process
      fileReader.addEventListener('load', () => {
        this.attachment = fileReader.result
      })
      //async conversion of file to base-64 string
      fileReader.readAsDataURL(files[0])
      this.attachmentRaw = files[0]
    },
    submitResearch () {
      //check if any compulsory field is empty
      if (this.location.trim() === '' 
      || this.researchDate.trim() === '' 
      || this.writeup.trim() === '') {
        alert('Please fill in all required fields.')
        return
      }
      //close the dialog and dispatch payload to store
      this.$store.dispatch('createResearch', {
        companyId: this.companyId,
        name: this.name,
        analyst: this.analyst,
        location: this.location,
        attachmentRaw: this.attachmentRaw,
        researchDate: this.researchDate,
        writeup: this.writeup,
        researchType: this.selectedResearchType,
        sentiment: this.selectedSentiment,
        addCompany: this.addCompany,
        createDate: new Date().toISOString().substr(0,10)
      })
      this.$router.push('/companies/' + this.companyId)
      this.researchDialog = false
    }
  }
}
</script>




