<template>
  <v-app>
    <v-navigation-drawer       
      temporary
      v-model="sideNav"
      absolute
    >
      <v-list>
        <v-list-tile 
        v-for="item in menuItems" 
        :key="item.title"
        :to="item.link"
        >
          <v-list-tile-action>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            {{ item.title }}
          </v-list-tile-content>
        </v-list-tile>
        <v-list-tile
        v-if="userAuthenticated"
        @click="onLogout"
        :to="'/'"
        >
          <v-list-tile-action>
            <v-icon>exit_to_app</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>Logout</v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>
    <v-toolbar 
    dark 
    class="green lighten-1"
    fixed
    >
      <v-toolbar-side-icon dark
      class="hidden-md-and-up" 
      @click.stop="sideNav = !sideNav">
      </v-toolbar-side-icon>
      <v-toolbar-title>
        <router-link to="/" tag="span" style="cursor: pointer">Startup Tracker</router-link>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items class="hidden-sm-and-down">
        <v-btn 
        flat 
        dark 
        v-for="item in menuItems" 
        :key="item.title"
        :to="item.link"
        >
          <v-icon left>{{ item.icon }}</v-icon>
          {{ item.title }}
        </v-btn>
        <v-btn
        v-if="userAuthenticated"
        @click="onLogout"
        :to="'/'"        
        flat>
          <v-icon left>exit_to_app</v-icon>
          Logout
        </v-btn>
      </v-toolbar-items>
    </v-toolbar>
    <main class="mt-5 pt-3">

    </main>
    <v-content>
      <router-view/>
    </v-content>
    <v-footer>
      <span>&copy; Startup Tracker 2018</span>
    </v-footer>
  </v-app>
</template>

<script>
import firebase from 'firebase'
export default {
  data () {
    return {
      sideNav: false
    }
  },
  computed: {
    //guard nav items according to login status
    menuItems () {
      let menuItems = [ 
        { icon: 'lock_open', title: 'Login', link: '/user/signin'}
      ]
      if (this.userAuthenticated) {
        menuItems = [
          { icon: 'view_list', title: 'Dashboard', link: '/companies'},
          { icon: 'add_box', title: 'Add Startup', link: '/companies/create'},
          { icon: 'person', title: 'Profile', link: '/user/profile'}  
        ]
      }
      return menuItems
    },
    userAuthenticated () {
      return this.$store.getters.user !== null && this.$store.getters.user !== undefined
    }
  },
  methods: {
    onLogout () {
      this.$store.dispatch('logout')
    }
  }
}
</script>

<style>
  .custom-loader {
    animation: loader 1s infinite;
    display: flex;
  }
  @-moz-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @-webkit-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @-o-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
</style>



