<template>
  <v-container fluid>
    <v-parallax 
    :src="require('@/assets/data.png')"
    height="600">
      <v-layout column align-center justify-center>
        <h1 class="green--text mb-4 mt-2">Startup Tracker</h1>
        <h2 class="green--text text-xs-center">Track the best startups around the world</h2>
      </v-layout>
      <v-layout column align-center justify-center>
        <v-flex xs12 sm6 class="text-xs-center text-sm-center">
          <v-btn large router to="/companies" class="success">Explore Startups</v-btn>
        </v-flex>
      </v-layout>
    </v-parallax>
    <v-layout row wrap align-center>
      <v-flex xs12 class="text-xs-center mt-4">
        <h1 class="text-xs-center mt-4">See what others are saying about Startup Tracker:</h1>
      </v-flex>
      <v-carousel class="mt-4">
          <v-carousel-item
            v-for="comment in comments"
            :src="comment.imageUrl"
            :key="comment.id">
            <div class="name">
              {{ comment.name }}: {{comment.comment}}
            </div>
          </v-carousel-item>
      </v-carousel>
    </v-layout>
  </v-container>
</template>

<script>
  export default {
    data () {
      return {
        comments: [
          { imageUrl: 'http://www.parkslopetherapist.com/wp-content/uploads/2015/05/man-tshirt-smiling.jpg', id: 'afajfjadfaadfa323', name: 'Peter Parker', comment: 'Startup Tracker is the best way to explore the startup world.' },
          { imageUrl: 'http://blogs.dctc.edu/dctc-news/files/2017/01/Scott_1-1050x735.jpg', id: 'aadsfhbkhlk1241', name: 'Tony Stark, C.F.A.', comment: 'Thanks to Startup Tracker, I quickly found the next big idea to invest in.' },
          { imageUrl: 'https://i2.wp.com/www.michaelsinkindds.com/wp-content/uploads/2014/12/man-smiling-hoodie.jpg', id: 'aadsfhbkhlk1242', name: 'Steve Rogers', comment: 'Startup job hunting has never been easier with Startup Tracker.' }
        ]
      }
    }
  }
</script>

<style scoped>
  .name {
    position: absolute;
    bottom: 50px;
    background-color: rgba(0,0,0,0.5);
    color: white;
    font-size: 1.5em;
    padding: 20px;
  }
</style>



