// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import { store } from './store'
import DateFilter from './filters/date'
import * as firebase from 'firebase'
import Alerts from './components/shared/alert.vue'
import EditCompanyDialog from './components/dialog/editCompanyDialog.vue'
import AddResearchDialog from './components/dialog/addResearchDialog.vue'
import FundingRoundDialog from './components/dialog/fundingRoundDialog.vue'
import EditResearchDialog from './components/dialog/editResearchDialog.vue'

import {
  Vuetify,
  VApp,
  VNavigationDrawer,
  VFooter,
  VList,
  VBtn,
  VIcon,
  VGrid,
  VToolbar,
  VParallax,
  VCarousel,
  VCard,
  VTabs,
  VDialog,
  VExpansionPanel,
  VForm,
  VTextField,
  VSelect,  
  VChip,
  VDataTable,
  VDatePicker,  
  VJumbotron,
  VAlert,         
  VProgressCircular,   
  VDivider,        
  VTooltip,      
  VSubheader,
  transitions
} from 'vuetify'
import '../node_modules/vuetify/src/stylus/app.styl'

Vue.use(Vuetify, {
  components: {
    VApp,
    VNavigationDrawer,
    VFooter,
    VList,
    VBtn,
    VIcon,
    VGrid,
    VToolbar,
    VParallax,
    VCarousel,
    VCard,
    VTabs,
    VDialog,
    VExpansionPanel,  
    VForm,
    VTextField,  
    VSelect,
    VChip,
    VDataTable,
    VDatePicker,
    VJumbotron,     
    VAlert,  
    VProgressCircular,   
    VDivider,  
    VTooltip,
    VSubheader,
    transitions
  },
  theme: {
    primary: '#ee44aa',
    secondary: '#424242',
    accent: '#82B1FF',
    error: '#FF5252',
    info: '#2196F3',
    success: '#4CAF50',
    warning: '#FFC107'
  }
})

Vue.config.productionTip = false

Vue.filter('dateFilter', DateFilter)
Vue.component('app-alert', Alerts)
Vue.component('app-edit-company-dialog', EditCompanyDialog)
Vue.component('app-add-research-dialog', AddResearchDialog)
Vue.component('app-funding-round-dialog', FundingRoundDialog)
Vue.component('app-edit-research-dialog', EditResearchDialog)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>',
  created () {
    firebase.initializeApp({
      apiKey: 'AIzaSyDyEbX-KRqSJRP6xobUiBzeAL6w1YTTAYA',
      authDomain: 'startup-tracker-super.firebaseapp.com',
      databaseURL: 'https://startup-tracker-super.firebaseio.com',
      projectId: 'startup-tracker-super',
      storageBucket: 'startup-tracker-super.appspot.com'
    })
    //check for existing user authentication
    firebase.auth().onAuthStateChanged((user) => {
      if (user) {
        this.$store.dispatch('autoLogin', user)
      }
    })
    this.$store.dispatch('loadCompanies')
    this.$store.dispatch('loadResearchDocs')
  }
})



