import Vue from 'vue'
import Router from 'vue-router'
import Welcome from '@/components/welcome'
import Companies from '@/components/company/companies'
import Company from '@/components/company/company'
import Create from '@/components/company/create'
import Research from '@/components/company/researchDoc'
import Profile from '@/components/user/profile'
import Signin from '@/components/user/signin'
import AuthGuard from './guard'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Welcome',
      component: Welcome
    },
    {
      path: '/companies',
      name: 'Companies',
      component: Companies,
      beforeEnter: AuthGuard
    },
    {
      path: '/companies/create',
      name: 'Create',
      component: Create,
      beforeEnter: AuthGuard      
    },
    {
      path: '/companies/:id',
      name: 'Company',
      props: true,
      component: Company,
      beforeEnter: AuthGuard      
    },
    {
      path: '/research/:id',
      name: 'Research',
      props: true,
      component: Research,
      beforeEnter: AuthGuard      
    },
    {
      path: '/user/profile',
      name: 'Profile',
      component: Profile,
      beforeEnter: AuthGuard      
    },
    {
      path: '/user/signin',
      name: 'Signin',
      component: Signin
    }
  ],
  mode: 'history'
})



